/* src/pages/Home.css */
.home {
        text-align : center;

    }
    .hero {
        position : relative;
        height : 600px;
        /* Adjust height as needed */
        overflow : hidden;
        margin-bottom : 50px;
        background-color : black;

    }
    .hero img {
        width: 100%;
        height: 100%;
        object-fit: cover;

    }
    .hero-text {
        position : absolute;
        top : 50%;
        left : 50%;
        transform : translate(-50%, -50%);
        color : white;
        font-weight : bold;
        text-shadow : 2 px 2 px 4 px rgba(0, 0, 0, 0.7);
    }
    .shop-now {
        padding : 10px 20px;
        font-size : 16px;
        background-color: #7c3616;
        /* Coffee color */
        color : white;
        border : none;
        cursor : pointer;
    };
    .featured-products {
        padding: 20px;
        margin: 10px;
    }
    .product-list {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
    .product {
        border: 1px solid #ccc;
        border-radius: 5px;
        margin : 30px;
        padding : 10px;
        /* width: 200px;
  /* Adjust width as needed */
    }
    .product img {
        width: 100%;
        height: auto;
    }
    /* Navbar */
    .navbar {
      background-color: #170E05;
        /* background-color : #7c360a; */
        padding : 0px;
        box-shadow : 0 px 2 px 2 px #6F4E37;
        position : fixed;
        width : 100%;
        z-index : 100;
        /* #7c3616 */

    }

    /* rgba(0, 0, 0, 0.1) */
    .navbar img {
        width: 15%;
        max-height: 7%;
        padding-left: 30px;
    }

    @media(max-width : 768px) {
            .navbar {

                padding-top : 10px;
                padding-bottom : 10px;
            }
            .navbar img {
                max-width: 50%;
                max-height: auto;
                padding-left: 20px;
            }
            .about-container {
                max-width : 768px;
                margin-left : 10px;
                padding : 20px;
                font-family : 'Arial',
                sans-serif;
                line-height : 1.6;
                color : #333;
                text-align : left;

            }
            .about-list {
                margin : 0;
                /* Removes default margin */
                padding : 0;
                /* Removes default padding */
                list-style-type : none;
                /* Remove default bullet points */
                display : flex;
                /* Use flexbox for the list */
                flex-direction : column;
                /* Stack items vertically */
                align-items : left;
                /* Center items horizontally */
          
            .about-list li {
                display: flex;
                /* Use flexbox for alignment */
                align-items: left;
                /* Center items vertically */
                margin: 5 px 0;
                /* Adds vertical spacing between each item */
                padding-left: 20px;
                /* Space for custom bullet */
                position: relative;
                /* Position for custom bullet */
            }

        }
      }

    @media(max-width : 375px) {
            .navbar {

                padding-top : 20px;
                padding-bottom : 20px;
            }
            .navbar-logo {
                max-width : 100%;
                max-height : auto;
                padding-left : 20px;
            }
            .small-screen-text {
                display : none;
            }
            .about-heading {
                font-size : 1.5 rem;
                /* Smaller heading size on small devices */
            }
            .about-subheading {
                font-size : 1 rem;
                /* Smaller subheading size on small devices */
            }
            .about-text {
                font-size : 0.9 rem;
                /* Smaller text size on small devices */
            }
            .about-list {
                align-items : center;
                /* Center align items */
            }
            .about-list li {
                padding-left: 10px;
                /* Adjust padding for smaller screens */
                font-size: 0.9rem;
                text-align: center;
            }
            .about-list li::before {
                width : 6 px;
                /* Smaller bullet size on small devices */
                height : 6 px;
                /* Smaller bullet size on small devices */
            }
            .a {
                padding-left : 8px;
            }
            .b {
                padding-left : 5px;

            }

