     .navbar-toggle {
       border: none;
       background-color: transparent;
       font-size: 2rem;
       color: white;
       cursor: pointer;
     }
  
     .navbar-menu {
       display: none;
       list-style: none;
       margin: 0;
       padding: 0;
     }
  
     .navbar-menu.active {
       display: flex;
       flex-direction: column;
       justify-content: center;
       align-items: center;
     }
  
     .navbar-item {
       margin: 0.5rem 0;
     }
  
     .navbar-link {
       font-size: 1.2rem;
       font-weight: normal;
       color: white;
       text-decoration: none;
     }
  
     .navbar-link:hover {
       box-shadow: 0px 2px 2px #8B4513;
     }
  
     /* Container styling */
     .about-container {
       max-width: 800px;
       margin: 0 auto;
       padding: 20px;
       font-family: 'Arial', sans-serif;
       line-height: 1.6;
       color: #333;
     }
  
     .about-heading {
       font-size: 2.5rem;
       margin-bottom: 20px;
       color: #7c360a;
       text-align: center;
     }
  
     .about-subheading {
       font-size: 1.8rem;
       margin: 20px 0 10px;
       color: #7c360a;
     }
  
     .about-text {
       font-size: 1rem;
       margin-bottom: 15px;
     }
  
     .about-list {
       margin: 0;
       padding: 0;
       list-style-type: none;
       display: flex;
       flex-direction: column;
       align-items: flex-start;
     }
  
     .about-list li {
       display: flex;
       align-items: center;
       margin: 5px 0;
       padding-left: 20px;
       position: relative;
       width: 100%;
     }
  
     .about-list li::before {
       content: '';
       position: absolute;
       left: 0;
       top: 50%;
       transform: translateY(-50%);
       width: 8px;
       height: 8px;
       background-color: black;
       border-radius: 50%;
     }
  
     .product-grid {
       display: grid;
       grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
       gap: 20px;
       padding: 20px;
     }
  
     .product-card {
       border: 1px solid #ddd;
       border-radius: 5px;
       padding: 15px;
       box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
       transition: box-shadow 0.3s ease-in-out;
       overflow: hidden;
     }
  
     .product-card img {
       width: 100%;
       height: auto;
       border-radius: 5px;
       display: block;
     }
  
     .product-card h2 {
       font-size: 1.5rem;
       margin: 10px 0;
     }
  
     .product-card p {
       font-size: 1rem;
       color: #777;
       margin: 5px 0;
     }
  
     .product-card button {
       padding: 10px 20px;
       background-color: #7c5b43;
       color: white;
       border: none;
       border-radius: 5px;
       cursor: pointer;
     }
  
     .product-card button:hover {
       background-color: #5a3e2d;
     }
  
     /* Responsive Design */
     @media (max-width: 768px) {
       .product-card {
         padding: 10px;
       }
  
       .product-card img {
         height: 200px;
         object-fit: cover;
       }
  
